<script>
import algoliasearch from 'algoliasearch/lite';
import 'instantsearch.css/themes/satellite-min.css';
import { history as historyRouter } from 'instantsearch.js/es/lib/routers';
import { singleIndex as singleIndexMapping } from 'instantsearch.js/es/lib/stateMappings';

export default {
  // components: { AppInfiniteHits },
  props: {
    item: Object
  },
  data() {
    return {
      searchInitFlag: false,
      upHereCurrentItemFlags : {},
      searchClient: algoliasearch(
        'SZOTLIDLJD',
        '5283f978a80eac551ed19eac8cfb6f3d'
      ),
      routing: {
        router: historyRouter(),
        stateMapping: singleIndexMapping('RECOMMEND_SHOONG_REL'),
      },
    }
  },
  created() {
  },
  methods: {
    searchFunction(helper) {
      if (this.searchInitFlag == true && helper.state.query.length > 1) {
        helper.search()
      } else {
        if (this.searchInitFlag == false) {
          this.searchInitFlag = true
          helper.search()
        }
      }
    },
    transformItems1(items, { results }) {
    //console.log(results)
      return items.map(item => ({
        ...item,
        label: item.isRefined
          ? `${item.label} (${results.nbPages} pages)`
          : item.label,
      }));
    },
    transformItems(items, { results }) {
      return items.map((item, index) => ({
        ...item,
        index: (index + 1 ) + (results.page * 7),
      }));
    },
    upHere(PROD_CODE, flag) {
      this.upHereCurrentItemFlags[PROD_CODE] = flag
    },
    upHereCurrentFlag(PROD_CODE){
      return this.upHereCurrentItemFlags[PROD_CODE]
    }
  },
 
}
